import qrCode1 from '@/static/mbcImg/publish/purchase/qrCode1.png';
import qrCode2 from '@/static/mbcImg/publish/purchase/qrCode2.png';
export default {
	data() {
		return {
			tabIndex: 0,
			dataLists:this.dataList,
			longTaps: true,
			qrCodeList: [
				qrCode1,
				qrCode2
			]
		};
	},
	props:[
		'dataList',
		'previewImage',
		'longTapSave',
		'isSaveBtn',
		'nextMargin',
		'marginLeft',
		'qrState'
	],
	mounted() {},
	watch: {
		dataList: {
			handler(a, b) {
				console.log(a, b, '-----------dataList----------');
				this.dataLists = a;
			},
			deep: true
		},
		qrState: {
			handler(a, b) {
				console.log(a, b, '-----------dataList----------');
				if (a !== "1") {
					this.dataLists = this.qrCodeList
				}
			},
			deep: true
		},
	},
	created() {
		if (this.longTap) {
			this.longTaps = this.longTap;
		}
	},
	methods: {
		niiii(){
			console.log(this.data)
		},
		swiperChange (e) {
			this.tabIndex = e.detail.current;
		},
		previewImageS (e, key) {
			console.log(e, '附件地址');
			if (this.qrState === "1") {
				if (this.previewImage === true) {
					let imgList = [];
					e.map((items, index) => {
						imgList.push(items)
					});
					if (this.longTapSave === true) {
						uni.previewImage({
							current: imgList[key],
							indicator: "number",
							loop: "true",
							urls:imgList,
							longPressActions: {
								itemList: ['保存图片'],
								success: function(data) {
									console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
								},
								fail: function(err) {
									console.log(err.errMsg);
								}
							}
						})
					} else {
						uni.previewImage({
							current: imgList[key],
							indicator: "number",
							loop: "true",
							urls:imgList
						})
					}
					
				}
			}
		},
		saveImage () {
			if (this.isSaveBtn === true) {
				this.dataLists.map((items, index)=>{
					let url = "https://" + items.split('://')[1];
					items = url;
					if (Number(index) === Number(this.tabIndex)) {
						uni.downloadFile({
							url: items,
							success: function (res) {
								console.log(res, '||||||||||||||')
								// h5













								// 小程序
								uni.saveImageToPhotosAlbum({        // 保存图片到本地
									filePath: res.tempFilePath,     // 图片临时路径
									success: function () { 
										uni.showToast({
											"title":"保存成功",
											duration:1000
										})
									},
								})

							}
						});
					}
				});
			};
		}
	},
};
